import React, { useState, useEffect} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from "@material-ui/core/Drawer";
import SideBar from "./sharedComponents/SideBar";
import Avatar from "@material-ui/core/Avatar";
import useStyles from "./styles/appBar";
import AboutMe from "./AboutMe";
import Portfolio from "./Portfolio";
import Contact from "./Contact"

export default function ButtonAppBar() {
  useEffect(() => document.title = "Scott Harwood",[])
  const pages = {"About Me": <AboutMe />,"Portfolio": <Portfolio/>,"Contact": <Contact />}
  const classes = useStyles()
  const [sideOpen,setSideOpen] = useState();
  const [currentPage, setCurrentPage] = useState();
  const onSetCurrentPage = (page) => {
    document.title="Scott Harwood: " + page;
    return setCurrentPage(page);
  }
  const toggleDrawer = (open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setSideOpen(open);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <IconButton 
            className={classes.menuButton}
            onClick={toggleDrawer(true)} 
            edge="start" 
            color="inherit"
            aria-label="menu"
          >
            <MenuIcon />
          </IconButton>
          
          <Drawer open={sideOpen} onClose={toggleDrawer(false)}>
            <SideBar toggleDrawer={toggleDrawer} setCurrentPage={onSetCurrentPage}/>      
          </Drawer>
        
          <Typography variant="h6" className={classes.title}>
              {currentPage}
          </Typography>
          
          <Avatar>SH</Avatar>
        </Toolbar>
      </AppBar>
      
      {pages[currentPage]}

    </div>
  );
}

